import React from 'react';
import { DotLottieReact } from '@lottiefiles/dotlottie-react';
import './AnimatedCharacter.css';

const AnimatedCharacter: React.FC = () => {
    return (
      <div
        style={{
          textAlign: 'center',
          marginTop: '100px',
          padding: '20px',
        }}
      >
        <DotLottieReact
          src="https://lottie.host/06693a84-faf2-47aa-b3f9-83335bfa3dea/uGq8SqLlme.lottie"
          loop
          autoplay
          className="character"
        />
      </div>
    );
  };

export default AnimatedCharacter;
