import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import AdbIcon from '@mui/icons-material/Adb';
import * as React from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import './TopBar.css';

function TopBar() {

  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <AppBar position="static" sx={{ backgroundColor: '#14161a' }}>
      <Container maxWidth="xl" sx={{ padding: { xs: '0 16px', sm: '0 24px' } }}>
        <Toolbar disableGutters>
          {/*  */}
          <AdbIcon
            sx={{
              display: { xs: 'flex', md: 'flex' }, // 
              mr: { xs: 0.5, md: 1 }, // 
              fontSize: { xs: '24px', md: '32px' }, // 
            }}
          />
          {/*  */}
          <Typography
            variant="h6"
            noWrap
            component="a"
            href="#app-bar-with-responsive-menu"
            sx={{
              mr: 2,
              display: 'flex',
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: { xs: '.1rem', md: '.3rem' }, // 
              fontSize: { xs: '16px', md: '20px' }, // 
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            English Typing Master
          </Typography>
            <Tabs
              value={value}
              onChange={handleChange}
              variant="scrollable"
              className="custom-tabs"
              sx={{
                flex: 1,
                overflow: 'hidden', // 
              }}
            >
				<Tab label="Easy Level" className="custom-tab" />
				<Tab label="Easy++ Level" className="custom-tab" />
				<Tab label="Medium Level" className="custom-tab" />
				<Tab label="Medium++ Level" className="custom-tab" />
				<Tab label="Hard Level" className="custom-tab" />
				<Tab label="Hard++ Level" className="custom-tab" />
				<Tab label="Daily Life" className="custom-tab" />
				<Tab label="Business" className="custom-tab" />
				<Tab label="Academic" className="custom-tab" />
				<Tab label="Slang" className="custom-tab" />
            </Tabs>
        </Toolbar>
      </Container>
    </AppBar>
  );
}

export default TopBar;
