import React, { useState } from 'react';
import words from '../../data/words.json'; // JSON
import './WordTyping.css'; // CSS

interface Word {
  japanese: string;
  english: string;
}

interface WordTypingProps {
  stageWords: Word[]; // 
  onCorrectAnswer: () => void; // 
}

const WordTyping: React.FC<WordTypingProps> = ({ stageWords, onCorrectAnswer }) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [userInput, setUserInput] = useState('');
  const [isCorrect, setIsCorrect] = useState(false);

  const currentWord = stageWords[currentIndex];

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setUserInput(value);

    // 
    if (value.trim() === currentWord.english) {
      setIsCorrect(true);
    } else {
      setIsCorrect(false);
    }
  };

  const handleNextWord = () => {
    setUserInput('');
    setIsCorrect(false);
    setCurrentIndex((prevIndex) => (prevIndex + 1) % words.length);

    // 
    onCorrectAnswer();
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    // Enter
    if (e.key === 'Enter' && isCorrect) {
      handleNextWord();
    }
  };

  return (
    <div className="word-typing-container">
      <h2 className="word-typing-japanese">{currentWord.japanese}</h2>
      <p className="word-typing-english">{currentWord.english}</p>
      <div className="word-typing-input-container">
        <input
          type="text"
          value={userInput}
          onChange={handleInputChange}
          onKeyDown={handleKeyDown}
          className={`word-typing-input ${isCorrect ? 'correct' : ''}`}
        />
        {isCorrect && (
          <img
            src={`${process.env.PUBLIC_URL}/images/enterkey.png`}
            alt=""
            onClick={handleNextWord}
            className="word-typing-enter-key"
          />
        )}
      </div>
    </div>
  );
};

export default WordTyping;
